




























import {Component, Vue, Watch} from 'vue-property-decorator';
import {Divider} from 'vant';
import 'vant/lib/divider/style';
import {AuthAdminModel, OperationType, TransactionStatus} from "../../model/auth_admin_model";

Vue.use(Divider);
@Component
export default class AuthAdminDetail extends Vue {

    private nowAuthRecord = new AuthAdminModel();
    private operationType = "";
    private targetAddress = "";
    private targetName = "";
    private authState = "";

    public created(): void {
        this.$store.state.title = this.$t('authRecord');
    }

    public async mounted() {
        this.nowAuthRecord = this.$store.state.nowAuthAdminRecord;
        console.log("mounted" + this.nowAuthRecord.name);
        console.log("mounted" + this.nowAuthRecord.caller);
        this.nowAuthRecord.caller.forEach(item => {
            console.log("item===>" + item.toString());
        });
        if (this.nowAuthRecord.operation_type === OperationType.ADD) {
            this.operationType = this.$t('authOriginal').toString();
        } else if (this.nowAuthRecord.operation_type === OperationType.EDIT) {
            this.operationType = this.$t('editAdmin').toString();
        } else if (this.nowAuthRecord.operation_type === OperationType.REMOVE) {
            this.operationType = this.$t('cancelOriginal').toString();
        } else {
            this.operationType = this.$t('unknownType').toString();
        }
        if (this.nowAuthRecord.txStatus === TransactionStatus.FINALLY) {
            this.authState = this.$t('authSuccess').toString();
        } else if (this.nowAuthRecord.txStatus === TransactionStatus.Expired) {
            this.authState = this.$t('authExpire').toString();
        } else {
            this.authState = this.$t('authPadding').toString();
        }
        this.targetAddress = this.nowAuthRecord.operator;
        this.targetName = this.nowAuthRecord.name;
    }
}
